.container {
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative; }
  .container :nth-child(1) {
    float: left;
    display: inline-block;
    height: 40px;
    background-color: #333;
    margin-left: -10px; }
  .container :nth-child(2) {
    height: 40px;
    background-color: #EEE; }
  .container :nth-child(3) {
    height: 40px;
    background-color: #222; }



