.container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url("images/background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden; }

.centerBox {
  width: 300px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -150px; }

.avatar {
  position: absolute;
  width: 90px;
  height: 90px;
  top: -105px;
  left: 50%;
  margin-left: -47px;
  border-radius: 45px;
  -webkit-transform: scale(1, 0.9);
      -ms-transform: scale(1, 0.9);
          transform: scale(1, 0.9);
  background-image: url("images/avatar.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid #FFF; }

.title {
  color: #FFF;
  font-size: 32px;
  text-align: center;
  font-family: Raleway, Verdana, sans-serif;
  letter-spacing: 4px; }

.titleText {
  font-size: 24px;
  cursor: pointer; }
  .titleText:hover {
    color: #EA8F8F; }

.titleUnderLine {
  width: 100%;
  height: 0;
  border-top: 1px solid #9C9C9C;
  opacity: 0.5;
  margin-top: 15px; }

.introduction {
  color: #FFF;
  font-size: 16px;
  margin-top: 15px;
  text-align: center; }

.catalog {
  background-color: rgba(225, 245, 215, 0.8) !important; }



